@import url(https://rsms.me/inter/inter.css);
body {
  margin: 10px;
  font-family: "Inter", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-transition: all 4s ease;
  transition: all 4s ease; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.App {
  text-align: center; }

.App-logo {
  height: 40vmin; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #09d3ac; }

h1 {
  text-align: center; }

.NavBar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden; }

.NavBar li {
  float: left; }
  .NavBar li a {
    display: block;
    color: deepskyblue;
    text-align: center;
    padding: 16px;
    text-decoration: none; }

.Calculator {
  width: 260px;
  display: inline-block; }
  .Calculator .result {
    display: block;
    height: 62px;
    width: 260px;
    text-align: right;
    font-size: 40px;
    overflow-x: auto;
    overflow-y: hidden; }
  .Calculator .resultSmall {
    line-height: 84px;
    font-size: 24px; }
  .Calculator .resultHelperText {
    position: absolute;
    text-align: left;
    -webkit-transform: translateX(270px) translateY(-30px);
        -ms-transform: translateX(270px) translateY(-30px);
            transform: translateX(270px) translateY(-30px); }
  .Calculator .expression {
    display: inline-block;
    width: 260px;
    text-align: right;
    margin-bottom: 10px; }
  .Calculator .accordionHeader {
    height: 30px;
    bottom: 30px;
    line-height: 0;
    margin-bottom: auto !important; }
  .Calculator .accordionHeader:hover {
    cursor: pointer; }
  .Calculator .defaultLink {
    color: deepskyblue !important; }
  .Calculator .defaultLink:hover {
    color: deepskyblue !important;
    cursor: pointer !important; }
  .Calculator .buttonContainer {
    display: inline-grid;
    grid-template-columns: repeat(4, 1fr); }
    .Calculator .buttonContainer button {
      width: 60px;
      margin: 3px;
      font-size: 20px; }
    .Calculator .buttonContainer .smallText {
      font-size: 14px;
      padding: 0;
      text-align: center; }

